<template>
    <div class="carousel">
        <div class="block">
            <el-carousel :height="height">
            <el-carousel-item v-for="(item,index) in imgURL" :key="index">
                <router-link :to="item.link">
                    <img :src="item.url"/>
                </router-link>
            </el-carousel-item>
            </el-carousel>
        </div>
  </div>
</template>

<script>
export default {
    name:"carousel",
    data(){
        return {
            screenWidth: document.body.clientWidth,
            height: '',
            imgURL: [
                {
                    url:require("../../assets/home/sourset/5cea6e3b31047.jpg"),
                    link:'/shualian'
                },
                {
                    url:require("../../assets/home/sourset/5c051dbab6173.jpg"),
                    link:'/shualian'
                },
                {
                    url:require("../../assets/home/sourset/banner.png"),
                    link:'/xiaobai'
                },
                {
                    url:require("../../assets/home/sourset/product_center_banner.png"),
                    link:'/xiaobai'
                },
            ]
        }
    },
    beforeMount:function(){
        this.height = document.documentElement.clientWidth*600/1920 + 'px';
        console.log(this.height);
    },
    mounted(){
        const that = this
        window.onresize = () => {
            return (() => {
                window.screenWidth = document.body.clientWidth
                that.screenWidth = window.screenWidth
                that.height = that.screenWidth*600/1920 + 'px'
            })()
        }
    },
}
</script>

<style>
.el-carousel.el-carousel--horizontal{
    width: 100%;
}
.block{
    width: 100%;
}
  .el-carousel__item img {
    height: 100%;
    width: 100%;
  }
  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }

</style>